import React from 'react';
import Calendar from 'react-calendar';

const calendar = () => (
    <div className='reservation-entry-dropdown reservation-entry-dropdown-hidden'>
        <Calendar 
            onChange={(date) => changeDate(date)}
        />
    </div>
);

const changeDate = (date) => {
    let dateString = `${date.getDate()}/${date.getMonth() + 1}/${date.getFullYear()}`;
    let calendarDrop = document.querySelector('.reservation-entry:nth-child(1)');
    calendarDrop.firstElementChild.firstElementChild.textContent = dateString;
    let dropdown = calendarDrop.children[1];
    dropdown.classList.remove('reservation-entry-dropdown-visible');
    dropdown.classList.add('reservation-entry-dropdown-hidden');
    let arrow = calendarDrop.firstElementChild.children[1].firstElementChild;
    arrow.classList.remove('arrow-up');
    arrow.classList.add('arrow-down');
}

export default calendar;