import React from 'react';
import '../css/Reserve.css';
import Entry from '../../entry/Entry';
import handleBookClick from '../../../../../utils/reserve/dropdown/HandleBookClick';
import BookingComplete from './BookComplete'; 


const reservation = (props) => (
    <div className='reservation'>
        <div className='reservation-title'>Reservations</div>
        <div className='reservation-entries-book-button'>
            <div className='reservation-entries'>
                <Entry type={'calendar'} text={'Date'} />
                <Entry type={'dropdown'} text={'Time'} />
                <Entry type={'dropdown'} text={'Persons'} />
            </div>
            <div className='reservation-book-button' onClick={(e) => handleBookClick(e)}>Reserve Seats</div>
        </div>
        <BookingComplete />
    </div>
);
export default reservation;