const handleDropdownClick = (e) => {
    let reservationParent = e.target.closest('.reservation-entry');
    let dropdownTarget = reservationParent.lastElementChild;
    let arrowTarget = reservationParent.firstElementChild.lastElementChild.firstElementChild;
    if(Array.from(dropdownTarget.classList).includes('reservation-entry-dropdown-hidden')) {
        makeDropdownVisible(dropdownTarget, arrowTarget);
        removeDropdowns(reservationParent.parentElement.children, reservationParent);
    }
    else {
        makeDropdownHidden(dropdownTarget, arrowTarget);
    }
}

const makeDropdownHidden = (dropdownTarget, arrowTarget) => {
    dropdownTarget.classList.remove('reservation-entry-dropdown-visible');
    dropdownTarget.classList.add('reservation-entry-dropdown-hidden');
    arrowTarget.classList.remove('arrow-up');
    arrowTarget.classList.add('arrow-down');
}

const makeDropdownVisible = (dropdownTarget, arrowTarget) => {
    dropdownTarget.classList.remove('reservation-entry-dropdown-hidden');
    dropdownTarget.classList.add('reservation-entry-dropdown-visible');
    arrowTarget.classList.remove('arrow-down');
    arrowTarget.classList.add('arrow-up');
}

const removeDropdowns = (allReservations, reservationParent) => {
    Array.from(allReservations)
        .filter((reservation) => reservation !== reservationParent)
        .forEach((reservation) => {
            makeDropdownHidden(
                reservation.lastElementChild,
                reservation.firstElementChild.lastElementChild.firstElementChild
            )
        });
}

