const getTimes  = () => (
    [...new Array(22).keys()].slice(9)
        .map((hour) => {
            let formattedtHour = hour < 10 ? `0${hour}` : `${hour}`;
            return getHour(formattedtHour);
        })
        .reduce((hours, hour) => {
            return [...hours, ...hour]; 
        }, [])
);

const getHour = (formattedHour) => (
    [...new Array(6).keys()]
        .map((mins) => `${formattedHour}:${mins}0`)
);

export default getTimes();