import { removeAllDropdowns } from './HandleClick';

const handleItemClick = (e) => {
    let entryTarget = e.target.closest('.reservation-entry');
    let allReservations = entryTarget.parentElement;
    let topTextTarget = entryTarget.firstElementChild.firstElementChild;
    topTextTarget.textContent = e.target.textContent
    removeAllDropdowns(allReservations);
}

export default handleItemClick;