import React from 'react';
import Dropdown from '../dropdown/Dropdown';
import Calendar from '../calendar/Calendar'
import handleDropdownClick from '../../../../utils/reserve/dropdown/HandleClick';
import handleItemClick from '../../../../utils/reserve/dropdown/HandleItemClick';


const entry = (props) => {
    if(props.type === 'calendar') {
        return (
            <div className='reservation-entry'>
                {textArea(props.text, (e) => handleDropdownClick(e))}
                <Calendar />
            </div>
        );
    }
    else {
        return(
            <div className='reservation-entry'>
                {textArea(props.text, (e) => handleDropdownClick(e))}
                <Dropdown itemClick={(e) => handleItemClick(e)} text={props.text} />
            </div>
        );
    }
}

export default entry;

const arrow = (
    <div className='arrow-wrapper'>
        <div className='arrow arrow-down'>
        </div>
    </div>
)

const textArea = (text, click) => (
    <div className='reservation-entry-text-arrow' onClick={click}>
        <div className='reservation-entry-text'>{text}</div>
        {arrow}
    </div>
);